/* Set up Tailwind CSS */
@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');

/* Apply Roboto font to all elements */
* {
  font-family: 'Roboto', sans-serif;
}

/* Styling for the home container */
.home-container {
  background-image: url('./assets/Background.png');
  background-size: cover;
  background-position: center;
  height: 100vh;
}

/* Styling for the login button with a gradient background */
.login-button {
  background-image: url('./assets/Gradient.png');
  background-size: auto;
  background-repeat: no-repeat;
}

/* Styling for elements with a gradient background */
.gradient {
  background-image: url('./assets/Gradient.png');
  background-size: cover;
}

/* Styling for a glass container with transparency */
.glass-container {
  background: rgba(255, 255, 255, 0.1); /* Adjust the alpha value for the level of transparency */
  padding: 26px;
  backdrop-filter: blur(10px); /* Adjust the blur value for the glass effect */
  border: 1px solid rgba(255, 255, 255, 0.2); /* Adjust the alpha value for the border transparency */
}
